@import url('https://fonts.googleapis.com/css?family=Amatic+SC');

.hint {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 45%;
  font-size: 3vh;
  color: white;
  font-family: 'Amatic SC';
  -webkit-animation: reveal 15s linear;
          animation: reveal 15s linear;
}

.Description {
  font-family: 'Amatic SC';
  font-size: 5vh;
  font-weight: 600;
  max-width: 60%;
  text-shadow: 1px 1px 1px black;
}

@keyframes reveal {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes reveal {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes scroll {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(12px);
    transform: translateY(12px);
  }
}

@keyframes scroll {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(12px);
    -ms-transform: translateY(12px);
    transform: translateY(12px);
  }
}

html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100vh;
  width: 100%;
  overflow-x: hidden;
}

body, #root {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.WhiteLayer {
  background: white;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 10vh;
  color: transparent
}

.Title {
  font-family: 'Amatic SC';
  font-size: 20vh;
  font-weight: bold;
  line-height: 1;
  position: relative;
  display: inline-block;
  margin: .25em;
  padding: .5em .75em;
  text-align: center;
  /* Color fallback */
  color: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.Title,
.Title:before {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  border-radius: 10px;
}

.BadBack {
  text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
}
.Title:before,
.Title:after {
    position: absolute;
    content: '';
    -webkit-box-sizing: border-box;
         box-sizing: border-box;
}

.Title:before {
    z-index: -2;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: inherit;
}

/* Text Background (black zone) */
.Title:after {
    position: absolute;
    z-index: -1;
    top: .125em;
    right: .125em;
    bottom: .125em;
    left: .125em;
    background-color: #000;
}

.SubRow {
  position: absolute;
  top: 100vh;
}

.DWork {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  -ms-flex-direction: column;
      flex-direction: column;
  color: white;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  z-index: 20;
}

.Work {
  height: 50vh;
  background-size: contain;
  background-repeat: no-repeat;
  width: 50%;
  overflow: hidden;
}

.Links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 10%;
}

.Links>hr {
  height: 3vh;
  color: white;
}

.Links>a {
  color: white;
  font-size: 5vh;
  text-shadow: 1px 1px 1px black;
}

.Links>a:visited {
  color: white;
  text-shadow: 1px 1px 1px black;

}



